<template>
  <div class="movie-list">
    <el-row>
      <el-col :span="6" v-for="(item, index) in movieList" :key="item.vid" >
        <router-link :to="`/movieDetail/${item.vid}`">
          <el-card :body-style="{ padding: '0px' }">
            <img :src="item.url" class="image">
            <div style="padding: 0px 8px 15px 8px;">
              <div style="font-size: 18px;margin-bottom:10px">{{item.title}}</div>
              <div class="content-detail" >{{item.content}}</div>
            </div>
          </el-card>
        </router-link>

      </el-col>
    </el-row>
  </div>

</template>

<style>

  .image {
    height: 100%;
    width: 100%;
    text-align: center;
  }
  .content-detail{
    text-align: left;word-break: break-all;

    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    color: #999;

  }

</style>

<script>
  import bg2 from "../../assets/bg2.jpg";

  const axios = require('axios')
  export default {
    name: 'Carousel',
    data: function () {
      return {
        active: '0',
        movieList: [],
      }
    },
    created (){
      axios({
        method: 'post',
        url: '/index?action=getNewVideo',
        data: {vid: 1002},
        header: {
          // 如果写成contentType会报错
          'Content-Type': 'application/json'
        }
      }).then(resp => {
        //服务端返回一个推荐视频数组
        if (!resp.data.videos) {
          this.$alert('获取信息失败！', '提示', {
            confirmButtonText: '确定'
          })
        } else {
          let videos = resp.data.videos
          for(let item of videos) {
            this.movieList.unshift({
              vid: item.vid,
              menuId: item.tid,
              title: item.title,
              content: item.text,
              url: 'https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png',
              videoSrc: item.video
            })
          }
          var storage=window.sessionStorage
          storage.setItem('movieList',JSON.stringify(this.movieList));
        }
      }).catch(failResponse => {
      })
    },
    methods: {

    }
  }
</script>



